.waves-effect{position:relative;overflow:hidden}.waves-effect .waves-ripple{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);opacity:0;width:0;height:0;border-radius:50%;background:rgba(255,255,255,0.5);animation:ripple-effect 0.4s ease}@keyframes ripple-effect{0%{opacity:0}25%{opacity:1}100%{width:200%;padding-bottom:200%;opacity:0}}
